"use client";
import { DOMAIN } from "@/constants";
import Image from "next/image";
import { useState } from "react";

interface ProductSingleImagesProps {
product: any
}

export const ProductSingleImages: React.FC<ProductSingleImagesProps> = ({ product}) => {
  const [activeImage, setActiveImage] = useState(product?.mainImage);


  return (
    <div className="w-1/2">
    <div className="flex">
      <div className="flex flex-col">
        {product?.mainImage && (
          <div className="mb-2">
            <Image
              src={`${DOMAIN}/${product?.mainImage}`}
              alt="product"
              width={57}
              height={57}
              onClick={() => setActiveImage(product?.mainImage)}
              className={`w-[56px] h-[56px] cursor-pointer ${
                activeImage === product.mainImage
                  ? "border border-blue-500"
                  : ""
              }`}
            />
          </div>
        )}
        {product?.images?.map((image: string, index: number) => (
          <div className="mb-2" key={index}>
            <Image
              src={`${DOMAIN}/${image}`}
              alt="product"
              width={57}
              height={57}
              onClick={() => setActiveImage(image)}
              className={`w-[56px] h-[56px] cursor-pointer ${
                activeImage === image ? "border border-blue-500" : ""
              }`}
            />
          </div>
        ))}
      </div>
      <div>
        <Image
          src={`${DOMAIN}/${activeImage}`}
          alt="product"
          width={400}
          height={400}
          className="w-[500px] h-[500px]"
        />
      </div>
    </div>
  </div>
  );
};

export default ProductSingleImages;
